/* Making dropdown contacts list  */
#contact-link {
  position: relative;
}
#contactData {
  display: block;
  position: absolute;
  align-items: start;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.2);
  color: var(--light-text-color);
  border-radius: 8px;
  top: 8%;
  right: 5%;
}

.list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
  padding: 5px 5px;
  cursor: pointer;
  border-bottom: 0.5px solid var(--primary-color);
}

.postsContent {
  display: none;
}

.postBlock {
  display: flex;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.4);
  color: black;
  justify-content: end;
  overflow: hidden;
  right: 0%;
  top: 0%;
  width: 100%;
  height: 100vh;
  gap: 20px;
  z-index: 3;
}
.button-close {
  position: absolute;
  right: 510px;
  margin-top: 5px;
  border-radius: 8px;
  border: none;
  background-color: var(--primary-color);
  color: var(--light-text-color);
  padding: 15px 20px;
  width: fit-content;
  height: fit-content;
  font-size: 1rem;
  font-weight: 600;
}

.posts-ul {
  padding: 15px 15px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 450px;
  width: 100%;
  background-color: white;
}
.posts-list {
  cursor: pointer;
  transition: 1s;
}
.posts-list:hover {
  text-decoration: underline;
}

.contact-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid var(--primary-color);
  border-radius: 8px;
}

