/* STYLE FOOTER */

.footer__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0px;
}

.footer__primary {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  gap: 44px;
  max-width: 1020px;
}

.footer__container {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.footer__container--social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.social-icon--list {
  list-style: none;
}

.footer-logo {
  padding: 13px 57px 13px 0px;
}

.hr-line {
  width: 100%;
  max-width: 1057px;
  border: 1px solid var(--line-color);
  margin: 0;
  padding: 0;
}

.footer__main {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 70px 0px;
}

.footer__nav {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  gap: 30px;
  width: 100%;
  justify-content: space-between;
}

.footer__nav--column {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.nav--colum__ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-submit {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.input-wrap {
  display: flex;
  border-radius: 5px;
  border: 1px solid #e6e6e6;
  background: #f9f9f9;
}

.error {
  border: 3px solid !important;
  box-shadow: inset 1px 1px 3px 3px rgba(255, 0, 0, 0.8) !important;
}
.succes {
  border: 3px solid !important;
  box-shadow: inset 1px 1px 2px 2px rgba(60, 179, 60, 0.8) !important;
}

.input-field {
  padding: 15px 0px 15px 20px;
  border: none;
  color: var(--muted-color);
  font-size: 14px;
  font-weight: 600;
  line-height: 28px;
}

.input-field:hover {
  color: black;
}

.input-button {
  background-color: var(--primary-color);
  color: var(--light-text-color);
  padding: 15px 22px 15px 22px;
  border-radius: 0px 5px 5px 0px;
  border: 1px solid #e6e6e6;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.6px;
  text-align: center;
}

.under-input {
  color: var(--muted-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 28px;
  letter-spacing: 0.4px;
  margin-top: -20px;
}

.nav--column__heading {
  color: var(--light-text-color);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0.1px;
}

.nav--column__li {
  list-style: none;
  color: var(--light-text-color);
}

.nav--column__link {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.2px;
  color: var(--muted-color);
}

.nav--column__link:hover {
  text-decoration: underline;
  color: var(--light-text-color);
}

.footer__secondary {
  width: 100%;
  background-color: var(--secondary-color);
  padding: 25px 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__secondary--rights {
  color: var(--light-text-color);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: 0.2px;
}
.footer__secondary--container {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 100px;
}

