@media (max-width: 575px) {
  .body {
    overflow: hidden;
  }
  .header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url(img/coverback-image.png);
    background-size: cover;
    box-sizing: border-box;
    overflow: hidden;
  }
  .container__choose-us {
    display: flex;

    /* max-width: 575px; */
  }
}

@media screen and (max-width: 640px) {
  .header__container {
    display: flex;
    flex-direction: column;
  }

  .header__container--navigation {
    margin-top: 40px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
    box-sizing: border-box;
  }

  .header__container--rigth-side {
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 15px;
  }

  .head__container--rigth-side__ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .nav-right {
    display: flex;
    align-items: center;
  }

  .header__container--heading__content {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  h1 {
    font-size: 18px;
    line-height: 40px;
    letter-spacing: 0.2px;
    margin: 0;
  }

  h4 {
    font-size: 12px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0.2px;
    margin: 0;
  }

  .button {
    font-size: 10px;
    padding: 2px 7px 2px 7px;
  }
}
@media (max-width: 768px) {
  .team-wraper {
    flex-direction: column;
  }

  .team-wraper__card {
    width: 100%; /* Take up full width */
  }
}
@media (max-width: 480px) {
  .team-wraper__card {
    padding: 10px; /* Reduce padding or adjust as needed */
  }
}
